<template>
  <div id="app">
    <div class="logo">
      <img src="./assets/logo.png" height="100" width="100" alt="Matt's Logo">
      <h1>Matt Gween</h1>
    </div>
    <div class="nav-bar">
      <router-link :to="'/home'" :class="{'active-page': $route.path === '/home'}">Home</router-link>
      <router-link :to="'/about'" :class="{'active-page': $route.path === '/about'}">About</router-link>
      <router-link :to="'/contact'" :class="{'active-page': $route.path === '/contact'}">Contact</router-link>
    </div>
    <transition name="route" mode="out-in">
      <router-view :key="$route.fullPath"></router-view>
    </transition>
  </div>
</template>

<script>
import Vue from 'vue';

import fontawesome from '@fortawesome/fontawesome';
import FontAwesomeIcon from '@fortawesome/vue-fontawesome';
import {
  faArrowCircleRight,
  faEnvelope,
  faPencilAlt,
  faRocket,
  faPaintBrush
} from '@fortawesome/fontawesome-free-solid';
Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.mixin({
  data() {
    return {
      server: location.host === 'localhost:8080' ? 'http://localhost:2626' : '',
      colors: [
        '#ffb3ba',
        '#ffdfba',
        '#ffffba',
        '#baffc9',
        '#bae1ff',
        '#b19cd9'
      ]
    }
  },
  methods: {
    shuffleArray(array) { // Source: https://stackoverflow.com/a/12646864
      for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      };
    }
  }
});

export default {
  name: 'App'
}
</script>

<style>
@font-face {
  font-family: Quicksand;
  src: url('/static/font/Quicksand-Regular.ttf');
}

body {
  background: #636363;
  font-family: 'Quicksand';
  overflow-x: hidden;
}
@media screen and (min-width: 769px) {
  body {
    margin: 0 20vw 1rem;
  }
}
.route-enter-active, .route-leave-active {
  transition: 0.75s;
}
.route-enter {
  transform: translateX(-160%);
}
.route-leave-to {
  transform: translateX(160%);
}
.collapse-enter-active, .collapse-leave-active {
  transition: all .3s;
}
.collapse-enter, .collapse-leave-to {
  transform: scale(0, 1);
}
input, textarea {
  background: rgba(0, 0, 0, 0);
  border: none;
  border-bottom: solid 2px #c9c9c9;
  transition: border 0.5s;
  outline: none;
  font-family: 'Quicksand', sans-serif;
}
textarea {
  border: solid 2px #c9c9c9;
}
input:focus {
  border-bottom: solid 2px #616161;
}
textarea:focus {
  border: solid 2px #616161;
}
.icon-button {
  color: #636363;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  transition: 0.5s;
}
.icon-button:hover {
  transform: scale(1.7);
  color: black;
  transition: 0.5s;
}

.logo {
  display: flex;
  align-items: center;
  margin: 1rem 0;
}
.logo > img {
  margin-right: 1rem;
}
.nav-bar {
  width: 100%;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-around;
  background: grey;
  z-index: 100;
}
.nav-bar > * {
  flex-grow: 1;
  text-align: center;
  text-decoration: none;
  font-size: 2em;
  color: white;
  transition: 1s;
}
.nav-bar > *:hover, .active-page {
  color: grey;
  background: #f2f2f2;
  transition: 1s;
}
</style>



